import FolderOutlined from '@ant-design/icons/lib/icons/FolderOutlined';
import TagOutlined from '@ant-design/icons/lib/icons/TagOutlined';
import { Button, Col, Row, Tag } from 'antd';
import { graphql, Link } from 'gatsby';
import { FluidObject } from 'gatsby-image';
import parse from 'html-react-parser';
import React from 'react';
import Comments from '../components/Comments';
import Layout from '../components/Layout';
import SEO from '../components/SEO';
import Sidebar from '../components/Sidebar';
import SocialMediaButtons from '../components/SocialMediaButtons';
import TopSection from '../components/TopSection';
import { CommentEdges } from '../contracts/comment';
import { CategoryTagInfo, Post } from '../contracts/post';
import '../styles/blog.scss';
import { capitalizeFirstLetter, removeHtmlTags } from '../utils';

export interface Props {
	data: {
		wpPost: Post;
		wpCategory: {
			posts: { nodes: Post[] }
		};
		allCommentsYaml: CommentEdges;
	};
	pageContext: {
		previous: {
			slug: string;
		}
		next: {
			slug: string;
		}
	};
	location: Location;
}

export const BlogPostPage = (props: Props) => {
	const fluid: FluidObject | null = (props.data.wpPost.featuredImage && props.data.wpPost.featuredImage.node && props.data.wpPost.featuredImage.node.localFile && props.data.wpPost.featuredImage.node.localFile.childImageSharp && props.data.wpPost.featuredImage.node.localFile.childImageSharp.fluid) ? props.data.wpPost.featuredImage.node.localFile.childImageSharp.fluid : null;
	const categories: CategoryTagInfo[] = (props.data.wpPost.categories && props.data.wpPost.categories.nodes! && props.data.wpPost.categories.nodes!.length) > 0 ? props.data.wpPost.categories.nodes!.filter((category) => category.name !== 'Uncategorized') : new Array<CategoryTagInfo>();
	const tags: CategoryTagInfo[] = (props.data.wpPost.tags && props.data.wpPost.tags.nodes! && props.data.wpPost.tags.nodes!.length) > 0 ? props.data.wpPost.tags.nodes! : new Array<CategoryTagInfo>();
	return (
		<>
			<TopSection title={props.data.wpPost.title} backgroundImage={fluid} author={props.data.wpPost.author.node?.name} slug={`author/` + props.data.wpPost.author.node?.slug} date={(props.data.wpPost.modified && props.data.wpPost.modified.length > 0) ? props.data.wpPost.modified : props.data.wpPost.date} />
			<Layout location={props.location} posts={props.data.wpCategory.posts.nodes.filter((post) => post.slug !== props.data.wpPost.slug).slice(0, 3)}>
				<SEO title={props.data.wpPost.title} image={fluid?.src} description={props.data.wpPost.excerpt} />
				<Row gutter={36} className="posts">
					<Col xs={24} sm={24} md={24} lg={16} xl={18} xxl={18} id="primary" className="content-area with-sidebar">
						<article className="post">
							<div className="post-content">{parse(props.data.wpPost.content)} </div>
							<div className="categories-container tags-container post-meta-container margin-bottom-24px">
								{categories && categories.length > 0 && categories.map((category, categoryIndex) => {
									return (
										<Tag key={categoryIndex}>
											<Link to={`/category/${category.slug}`} title={category.name}>
												<FolderOutlined />{capitalizeFirstLetter(category.name)}
											</Link>
										</Tag>
									);
								})}
								{tags && tags.length > 0 && tags.map((tag, tagIndex) => {
									return (
										<Tag key={tagIndex}>
											<Link to={`/tag/${tag.slug}`} title={tag.name}>
												<TagOutlined />{capitalizeFirstLetter(tag.name)}
											</Link>
										</Tag>
									);
								})}
							</div>
							<div className="navigation-links margin-bottom-24px">
								{props.pageContext.next && props.pageContext.next.slug && (
									<Link to={`/${props.pageContext.next.slug}`} title={props.pageContext.next.slug}>
										<Button type="primary">Go to Previous Post</Button>
									</Link>
								)}
								{props.pageContext.previous && props.pageContext.previous.slug && (
									<Link to={`/${props.pageContext.previous.slug}`} title={props.pageContext.previous.slug}>
										<Button type="primary">Go to Next Post</Button>
									</Link>
								)}
							</div>
						</article>
						<SocialMediaButtons url={`${process.env.GATSBY_SITE_URL}/${props.data.wpPost.slug}`} title={props.data.wpPost.title} description={removeHtmlTags(props.data.wpPost.excerpt)} />
						<hr />
						{process.env.GATSBY_ENABLE_COMMENTS &&
							<Comments slug={props.data.wpPost.slug} />
						}					</Col>
					<Col xs={0} sm={0} md={0} lg={8} xl={6} xxl={6} id="secondary" className="sidebar">
						<Sidebar posts={[{ node: props.data.wpPost }]} />
					</Col>
				</Row>
			</Layout>
		</>
	);
};

export default BlogPostPage;

export const query = graphql`
	query($id: String!, $firstCategoryId: String!) {
		wpCategory(id: {eq: $firstCategoryId}) {
			posts {
			  nodes {
				id
				title
				excerpt
				slug
				featuredImage {
					node {
					  altText
					  localFile {
						childImageSharp {
							fluid(maxWidth: 1000, quality: 100) {
								...GatsbyImageSharpFluid_tracedSVG
							}
						}
					  }
					}
				}
				categories {
					nodes {
						name
						slug
					}
				}
			  }
			}
		}
		wpPost(id: { eq: $id }) {
			title
			content
			excerpt
			date(formatString: "MMMM DD, YYYY")
			modified(formatString: "MMMM DD, YYYY")
			author {
				node {
					id
					name
					url
					description
					slug
				}
			}
			slug
			id
			featuredImage {
				node {
				  altText
				  localFile {
					childImageSharp {
						fluid(maxWidth: 1000, quality: 100) {
							...GatsbyImageSharpFluid_tracedSVG
						}
					}
				  }
				}
			}
			categories {
				nodes {
					id
					link
					count
					description
					name
					slug
				}
			}
			tags {
				nodes {
					id
					link
					count
					description
					name
					slug
				}
			}
		}
	}
`;
