import Col from 'antd/lib/grid/col';
import Row from 'antd/lib/grid/row';
import React from 'react';
import {
	FacebookShareButton,
	FacebookIcon,
	TwitterShareButton,
	TwitterIcon,
	EmailShareButton,
	EmailIcon
} from 'react-share';
import './SocialMediaButtons.scss';

export interface Props {
	title: string;
	url: string;
	description: string;
}

export const SocialMediaButtons = (props: Props) => {
	return (
		<Row justify="end" gutter={5}>
			<Col>
				<FacebookShareButton
					url={props.url}
					className="socialMediaButton">
					<FacebookIcon size={36} round />
				</FacebookShareButton>
			</Col>
			<Col>
				<TwitterShareButton
					url={props.url}
					title={props.title}
					className="socialMediaButton">
					<TwitterIcon size={36} round />
				</TwitterShareButton>
			</Col>
			<Col>
				<EmailShareButton
					url={props.url}
					subject={props.title}
					body={props.description}
					className="socialMediaButton">
					<EmailIcon size={36} round />
				</EmailShareButton>
			</Col>
		</Row>
	);
};

export default SocialMediaButtons;
